.blog-comments-container {
    width: 100%;
    max-height: 0px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    overflow: hidden;
    transition: max-height 0.5s ease-in-out;
    &__yours-comment-blocked {
        font-size: 16px;
        text-align: center;
        margin-bottom: 35px;
        margin: 0 10px;
        & a {
            font-weight: 700;
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }
        }
    }
}

.blog-comments-container-opened {
    max-height: 100%;
}

.blog-comments-container-closed {
    max-height: 0px;
}

@media screen and (max-width: 700px) {
    .blog-comments-container {
        &__yours-comment-blocked {
            font-size: calc( (100vw - 20rem)/(80 - 30) * (1.5 - 1) + 0.7rem);
        }
    }
}